@media screen and (max-width: 991px) {
    header{
        .nav-list{
            margin: 0 auto;
            padding: 12px;
        }
        .nav-account{
            margin: 10px auto 0;
        }
    }

    .first-screen{
        .first-screen_action{
            .base{
                height: auto;
                margin: 0 auto;
                max-width: 500px;
                .saving{
                    text-wrap: wrap;
                }
                .time-saving{
                    padding-left: 8px;
                    text-wrap: wrap;
                }
            }
        }

        .first-screen_title{
            font-size: 42px;
            margin-top: 10px;
        }

        .first-screen_img{
            svg{
                width: 300px;
                height: 300px;
                bottom: 250%;
            }
        }
    }
}