header{
    background-color: var(--text-color-dark);
    padding-top: 60px;
    .nav{
        justify-content: center;
    }
    .nav-logo{
        justify-content: center;
        display: flex;
        a{
            justify-content: center;
            display: flex;
        }
    }
    .nav-list{
        display: flex;
        gap: 32px;
        margin-left: auto;
        margin-right: 57px;
        padding: 12px 0;
        .nav-item{
            color: var(--text-color);
            text-decoration: none;
            line-height: 28px;
            letter-spacing: 0.16px;
        }
        .nav-item:hover{
            color: #3671E9;
        }
    }
    .nav-account{
        a{
            color: var(--text-color);
        }
        .login-nav{
            align-items: center;
            display: inline-flex;
            font-weight: 500;
            line-height: 150%;
        }
        .login-nav:hover{
            color: #3671E9;
        }
        .login-nav::after{
            content: "";
            margin: 0 20px;
            margin-left: 24px;
            opacity: 0.3;
            background: #F2F2F2;
            display: inline-block;
            width: 1px;
            height: 24px;
        }
        #register{
            padding: 13px 30px;
            padding-right: 32px;
            border-radius: 32px;
            background-color: #3671E9;
            font-weight: 500;
            height: 52px;
            text-align: center;
            transition: all 0.3s;
        }
        #register:hover{
            background-color: #fff;
            color: var(--text-color-dark);
        }
    }
}