.choice{
    .statistics-company{
        padding-bottom: 180px;
        .info-statistics_container{
            gap: 63px;
            flex-wrap: nowrap;
            z-index: 2;
            position: relative;
        }
        .info-statistics{
            max-width: 370px;
            .row{
                padding: 0 12px;
            }
        }
        .img-container{
            border-radius: 50%;
            background-color: #252540;
            display: flex;
            justify-content: center;
            align-items: center;
            max-width: 80px;
            max-height: 80px;
        }
        .statistic-title{
            color: var(--text-color);
            font-size: 40px;
            font-weight: 700;
            line-height: 150%;
            margin-bottom: 2px;
        }
        .statistic-subtitle{
            color: var(--text-color);
            font-size: 16px;
            font-weight: 400;
            line-height: 175%;
            letter-spacing: 0.16px;
        }
        .col-content{
            padding: 2px 24px 0;
        }
    }
    .container-page_choice{
        gap: 108px;
    }
    .img-container_choice{
        max-width: 50%;
        padding-top: 39px;
    }
    .choice-container_content{
        color: var(--text-color);
        max-width: 50%;
        .choice-title{
            font-size: 40px;
            font-weight: 700;
            line-height: 150%;
            margin-bottom: 22px;
        }
        .choice-subtitle{
            font-size: 16px;
            font-weight: 400;
            line-height: 175%;
            letter-spacing: 0.16px;
            max-width: 408px;
            margin-bottom: 32px;
        }
        .choice-btn{
            font-size: 18px;
            font-weight: 500;
            line-height: 150%;
            padding: 16px 24px;
        }
    }
}