.first-screen{
    position: relative;
    background-color: var(--text-color-dark);
    margin-bottom: 200px;
    .col{
        padding-top: 131px;
        position: relative;
        z-index: 2;
    }
    .first-screen_action{
        animation-delay: 2s;
        .base{
            display: flex;
            max-width: 365px;
            width: 100%;
            height: 40px;
            padding: 4px 5px;
            border-radius: 32px;
            background: #ffffff1a;
            .saving{
                padding: 4px 16px;
                border-radius: 32px;
                background-color: #fff;
                font-weight: 500;
                letter-spacing: 0.64px;
                text-transform: uppercase;
                display: inline-flex;
                text-wrap: nowrap;
            }
            .time-saving{
                padding-top: 4px;
                padding-left: 16px;
                line-height: 28px;
                letter-spacing: 0.16px;
                color: var(--text-color);
                text-wrap: nowrap;
            }
        }
    }
    .first-screen_title{
        margin-top: 24px;
        font-size: 64px;
        font-weight: 700;
        line-height: normal;
        color: var(--text-color);
    }
    .first-screen_subtitle{
        max-width: 435px;
        margin-top: 24px;
        line-height: 28px;
        letter-spacing: 0.16px;
        color: var(--text-color);
        animation-delay: 0.5s;
    }
    .first-screen_button{
        box-shadow: none;
        margin-top: 32px;
        display: inline-flex;
        padding: 16px 16px 15px 24px;
        align-items: center;
        gap: 24px;
        border-radius: 32px;
        background: #3671E9;
        color: var(--text-color);
        font-size: 18px;
        font-weight: 500;
        animation-delay: 1s;
        svg{
            width: 32px;
            height: 32px;
            padding: 4px;
            background-color: #fff;
            border-radius: 16px;
        }
    }
    .first-screen_button:hover{
        cursor: pointer;
        background-color: #fff;
        color: black;
        svg{
            background-color: #3671E9;
            path{
                fill: #fff;
            }
        }
    }


    .first-screen_img{
        position: relative;
        z-index: 1;
        padding-bottom: 37px;
        animation-duration: 3s;
        svg{
            position: absolute;
            right: 1px;
            bottom: 0;
            .img-animation_svg{
                animation-duration: 7s;
                animation-delay: 2s;
                animation-timing-function: linear;
                animation-name: svg_first-screen_an;
                animation-iteration-count: infinite;
            }
            @keyframes svg_first-screen_an {
                0%{
                    transform: translate(0px, 0px);;
                }
                50%{
                    transform: translate(0px, -30px);;
                }
                100%{
                    transform: translate(0px, 0px);;
                }
            }
            .svg1{
                animation-delay: 2s;
            }
            .svg2{
                animation-delay: 3s;
            }
            .svg3{
                animation-delay: 4s;
            }

            .svg_bitcon{
                animation-delay: 2s;
                animation-iteration-count: infinite;
                animation-timing-function: linear;
                animation-duration: 7s;
                animation-name: bitcon_svg-an_first-screen;
            }
            @keyframes bitcon_svg-an_first-screen {
                0%{
                    fill: #42E8E0;
                }
                50%{
                    fill: #732ccf;
                }
                100%{
                    fill: #42E8E0;
                }
            }
        }
    }

    .first-screen_bg{
        position: absolute;
        right: 0;
        bottom: -80px;
    }
    .first-screen_bg1{
        position: absolute;
        right: 356px;
        bottom: -250px;
    }
}