*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Rubik, sans-serif;
}
body{
    background-color: var(--text-color-dark);
    overflow-x: hidden;
}
a{
    text-decoration: none;
}
.container-xxl{
    max-width: 1226px;
    padding-left: 13px;
}
.btn-default{
    box-shadow: none;
    display: inline-flex;
    align-items: center;
    border-radius: 32px;
    background: #3671E9;
    color: var(--text-color);
    cursor: pointer;
}
.btn-default:hover{
    background-color: #fff;
    color: black;
}